  /*↓デフォルトのアイコンを消す場合はコメントアウト（noneにする）*/
  appearance: auto;
  /*↓デフォルトではないアイコンを使う場合は入力する*/
}
[type=date]:focus {
  border-color: #c94800;
}

input[type=date]::-webkit-calendar-picker-indicator {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}

textarea {
  display: block;
  border: 1px solid #408f95;
  height: 12.5rem;
  width: 100%;
  max-width: 30rem;
  resize: none;
}

input::placeholder {
  color: #ddf0f1;
}

input:user-invalid,
textarea:user-invalid {
  border-color: #c94800;
  background: rgba(201, 72, 0, 0.2);
}

input:user-invalid::placeholder {
  color: #fff;
}

[type=checkbox]:user-invalid + span::before,
[type=redio]:user-invalid + span::before {
  border-color: #c94800;
  background: rgba(201, 72, 0, 0.2);
}

.wpcf7-form.invalid input.wpcf7-not-valid,
.wpcf7-form.invalid textarea.wpcf7-not-valid {
  border-color: #c94800;
  background: rgba(201, 72, 0, 0.2);
}
.wpcf7-form.invalid input.wpcf7-not-valid::placeholder {
  color: #fff;
}
.wpcf7-form.invalid span.wpcf7-not-valid .wpcf7-list-item-label::before {
  border-color: #c94800;
  background: rgba(201, 72, 0, 0.2);
}