@use "global" as *;

.sub-page-content {
  font-family: $base-font-family-ja;
  color: $darkgreen;
}

.sub-page-content__title {
  font-size: rem(24);
  font-weight: $medium;

  @include mq("md") {
    font-size: rem(32);
  }
}

.sub-page-content__box {
  margin-top: rem(40);
}

