.wp-block-group {
  margin-top: rem(32);
}

.sub-page-content__box p,
.sub-page-content__box li {
  font-size: rem(16);
  font-weight: $normal;
  line-height: 2;
}

.sub-page-content__box ul,
.sub-page-content__box ol {
  list-style: decimal;
  padding-left: rem(22);
  text-indent: rem(2);
}
