[type="radio"],
[type="checkbox"] {
  // display: none; // コンソールエラーになるので×
  // appearance: none;
  position: absolute;
  opacity: 0;

  + span {
    display: inline-block;
    position: relative;
    padding: 0 0 0 calc($check-size + $check-space);
    margin: 0 $check-space-betweenRadio 0 0;
    font-size: $check-fz;
    line-height: 1;
    cursor: pointer;
    transition: 0.3s ease;

    &::before {
      position: absolute;
      content: "";
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background: $check-bg-cr;
      border: 1px solid $check-border-cr;
      width: $check-size;
      height: $check-size;
      display: block;
    }

    &::after {
      opacity: 0;
      transition: 0.3s ease;
    }
  }

  &:checked + span {
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      display: block;
      opacity: 1;
      /*チェックマークじゃない場合は以下を変更する*/
      transform: translateY(-50%) rotate(-45deg);
      // width: calc($check-size * 0.9);
      // height: calc($check-size * 0.5);
      width: rem(9);
      height: rem(5);
      margin-top: -0.1em;
      margin-left: 0.1em;
      border-left: $check-weight solid $check-bg-cr-inner;
      border-bottom: $check-weight solid $check-bg-cr-inner;
    }
  }
}

select {
  /*↓リセットCSS */
  border-radius: 0;
  font-family: inherit;
  color: inherit;
  border: none;
  cursor: pointer;
  width: 100%;
  /*↓カスタム */
  max-width: rem(480);
  margin-top: rem(16);
  // font-size: $fz;
  font-size: rem(12);
  // padding: $padding-und $padding-lnr;
  padding: rem(9) rem(7);
  background-color: $back-cr;
  border: 1px solid $border-cr-default;
  border-radius: $border-r;

  @include mq("md") {
    margin-top: 0;
  }
  /*↓デフォルトのアイコンを消す場合はコメントアウト（noneにする）*/
  // appearance: auto;
  /*↓デフォルトではないアイコンを使う場合は入力する*/
  // background-image: url();
  // background-position: right 12px center;
  // background-repeat: no-repeat;

  &:focus {
    border: 1px solid $border-cr;
  }
}

[type="date"] {
  position: relative;
  /*↓デフォルトのアイコンを消す場合はコメントアウト（noneにする）*/
  appearance: auto;
  /*↓デフォルトではないアイコンを使う場合は入力する*/
  // background-image: url();
  // background-position: right 12px center;
  // background-repeat: no-repeat;
  &:focus {
    border-color: $border-cr;
  }
}

input[type="date"]::-webkit-calendar-picker-indicator {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}

textarea {
  display: block;
  border: 1px solid $border-cr-default;
  height: $textarea-height;
  width: 100%;
  max-width: rem(480);
  resize: none;
}

input::placeholder {
  color: #ddf0f1;
}

input:user-invalid,
textarea:user-invalid {
  border-color: #c94800;
  background: rgba(201, 72, 0, 0.2);
}

input:user-invalid::placeholder {
  color: $white;
}

[type="checkbox"]:user-invalid + span::before,
[type="redio"]:user-invalid + span::before {
  border-color: #c94800;
  background: rgba(201, 72, 0, 0.2);
}

// WPカスタマイズ用（Contactform7）

// 送信ボタンを押して、必須項目が入力されていない場合
.wpcf7-form.invalid {
  input.wpcf7-not-valid,
  textarea.wpcf7-not-valid {
    border-color: #c94800;
    background: rgba(201, 72, 0, 0.2);
  }

  input.wpcf7-not-valid::placeholder {
    color: $white;
  }

  span.wpcf7-not-valid {
    .wpcf7-list-item-label::before {
      border-color: #c94800;
      background: rgba(201, 72, 0, 0.2);
    }
  }
}
